.qa-travel {
    height: 160px;
    width: 100%;
    @include brgImg('../images/q&abrg.png');
    &__center {
        display: flex;
        justify-content: center;
        height: 100%;
    }
    &__question {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-flow: wrap;
        margin: 0 -15px;
        &__text {
            color: $main-dark !important;
            span {
                font-weight: bold;
            }
            font-size: 30px;
        }
        &__btn {
            color: $main-dark !important;
            width: 135px;
            height: 45px;
            background-color: #ffffff;
            border-radius: 5px;
            text-transform: uppercase;
            text-align: center;
            line-height: 45px;
        }

    }
}

.footer-menu {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: 215px;
    grid-gap: 30px;
    &__item {
        display: flex;
        align-items: center;
        height: 100%;
        justify-content: space-between;
    }
    a {
        line-height: 50px;
        text-transform: uppercase;
        color: #4c4c4c;
    }
    &__logo {
        margin: 0 auto;
    }
    


}
.copyright-style3 {
    @extend .copyright;
    position: unset ;

    .copyright__payment-method {
        img {
            vertical-align: middle;
        }
        margin-left: 30px;
        &:first-child {
            margin: 0;
        }
    }
}


