@include sectionTittle(top-desti);
.top-desti {
    // &::after {
    //     content: '';
    //     width: 100%; /* Ensures there are at least 2 lines of text, so justification works */
    //     display: inline-block;
    // }
    // text-align: justify;
    // padding: 0 30px;
    display: flex;
    padding: 0 15px;

    &__item {
        margin: 0 15px;
        display: inline-block;
        border-radius: 5px;
        overflow: hidden;
        position: relative;
        img{
            vertical-align: middle;
            transition: all 0.5s;
        }
        &:hover {
            img{
                transform: scale(1.2);
            }
        }
    }
    &__ammout {
        padding: 0 20px;
        width: 100%;
        bottom: 18px;
        display: flex;
        justify-content: space-between;
        position: absolute;
        span {
            i {
                margin-right: 5px;
                color: $yellow;
                font-size: 18px;
            }
            @include fontface ($font-primary,16px,#ffffff,normal);
        }
    }
}