#header-1 {

	position: relative;
	height: 910px;
	@include brgImg('../images/header1-banner.jpg');
}

.header-content1 {
	height: 110px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	nav {

		border-radius: 7px;
		transition: 0.5s;
		ul {
			list-style-type: none;
	
		}
	}
	li {
		display: inline-block;
		position: relative;


		ul {
	
			background-color: rgba($color: #ffffff, $alpha: 0.8);
			display: none;
			border-top: 3px solid $yellow;
			margin: 0;
			position: absolute;
			li{
				width: 180px;
				border-bottom: 1px solid $dusty-gray;
				&:last-child {
					border-bottom: 1px solid transparent;
				}
			}
			a{	

				color: $main-dark;
				padding: 7px;
			}

		}
		a {
			i {
				display: none;
			}
			font-family: $font-primary-bold;
			display: block;
			padding: 40px 16px 30px;
			color: #ffffff;
			border-radius: 0 0 5px 5px;
			&:hover {
				background-color: $yellow;
				color: $main-dark;
			}
		}
	}
	&__hamburger {
		display: none;
		span {
			transition: all 0.5s;
			width: 30px;
			height: 5px;
			background-color: rgba($color: #ffffff, $alpha: 0.9);
			display: block;
			border-radius: 6px 6px 2px 2px;
			margin-bottom: 4px;
		}
	}

}
.header-tittle1 {
	text-align: center;
	margin-top: 205px;
}

#form-1-area {
	bottom: 30px;
	width: 100%;
    position: absolute;
}

// hamburger effect


.faded {
	background-color: rgba($color: #ffffff, $alpha: 0.4) !important;
}

