.header-content3 {
  
    &__mobile-logo {
        display: none ;
    }
    height: 110px;
    &__logo {
        margin: 0 57px 0 90px;
    }
    .header-3-nav {    
   
        transition: all 0.5s;
        ul {
       
            margin: 0;
            list-style-type: none;
            z-index: 2;
            @media(min-width: 1024px){
                height: 110px;
                display: flex;
                justify-content: center;
                align-items: center;
            }

        }
        li {
            position: relative;
            height: 100%;
            @media(min-width: 1024px){
                display: flex;
                justify-content: center;
                align-items: center;
            }
 
           
            & > ul {
                @media (min-width:1024px) {
                    border-top: 3px solid $yellow;
                    top: 110px;
                    position: absolute;
                    background-color: rgba($color: #ffffff, $alpha: 0.8);
                }
            
                display: block;
                height: auto;
                li{
                    a {
                        margin: 0;
                    }
                    width: 180px;
                    border-bottom: 1px solid #e6e6e6;
                    &:last-child {
                        border-bottom: 1px solid transparent;
                    }

                }
                display: none;
            }
        
            a:not(.header-content3__logo__sitename) {
                .fa-angle-down{
                    display: none;
                }
                color: $main-dark;
                font-family: $font-primary-medium;
                display: block;
                padding: 5px 2px;
                margin-left: 17px;
                margin-right: 17px;
                border-bottom: 2px solid transparent;
                &:hover {
                    font-family: $font-primary-bold !important; 
                    color: $yellow !important;
                    border-bottom: 2px solid $yellow !important;
                }
            }
        }
    }
    


    &__hamburger {
        @extend .header-content2__hamburger;
        span {
            background-color: $yellow;
        }
    }

}

