.gallery-syncing-item {
    margin-top: 25px;
    .slick-prev {
        z-index: 1;
        left: 15px;
        &::before {
            font-size: 20px;
        }
    }
    .slick-next {
        &::before {
            font-size: 20px;
        }
        right: 15px;
    }
    &__nav {
        &__item {
            img {
                margin: 0 auto;
                width: 95%;
            }
        }
        .is-active {
            img {
                filter: brightness(40%)
            }
        }
    }
}
@media(min-width:$xl){
    .gallery-syncing-item__nav__item {
        img {
            height: 140px;
        }
    }
}