.destination-mansory-layout
{
    &__item { 
        overflow: hidden;
        border-radius: 5px;
        img {
            width: 100%;
            height: 100%;
            transition: all 0.5s;
        }
        &:hover {
            & > img {
                transform: scale(1.2);
            }
            .destination-mansory-layout__item__tour-info {
                padding: 22px 45px;
                &__line {
                    visibility: visible;
                    width: 35px;
                }
                span {
                    color: #ffffff;
                }
            }
            
        }
        position: relative;
        display: block;
        text-align: center;
        &__tour-info {
            top: 0;
            left: 0;
            transition: all 0.5s;
            padding: 22px 30px;
            position: absolute;
            p {
                @include fontface($font-primary-medium,20px,#ffffff, normal);
                text-transform: uppercase;
                margin-bottom: 5px;
            }
            span {   
                transition: all 0.5s;
                @include fontface($font-primary,15px,transparent, normal);
            }
            &__line {
                transition: all 0.5s;
                position: absolute;
                border-top: 3px solid #ffffff;
                width: 0px;
                left: 0;
                top: 37px;
            }
        }
    }
    margin-top: 65px;
    &__pagination {
        padding: 50px 0 100px;
    }
}


@media (min-width:1024px) {
    .destination-mansory-layout {
    
        display: grid;
        grid-template-columns: 22.82% 17.57% 31.2% auto;
        grid-auto-rows: 285px ;
        column-gap: 30px;
        row-gap: 30px;
        &__item { 
            img {
                width: 100%;
                height: 100%;
            }

            &--hor-2 {
                grid-column-start:2;
                width: 177.54%;
            }
    
            &--nor-2{
                grid-column-start: 3;
                justify-self: flex-end;
                width: 56.32%;
            }
            &--ver-2 {
                grid-row-start: span 2;
            }
            &--ver-1 {
                grid-column-start: 1;
                grid-row-start: span 2;
            }
        }
    }
}

@media(max-width:1651px){

    .destination-mansory-layout {
       
        grid-auto-rows: 200px ;
        column-gap: 20px;
        row-gap:20px;
    }
}
@media (min-width:$sm)and(max-width:1023px) {
    .destination-mansory-layout {
        display: grid;
        grid-template-columns: 35% auto 35%;
        grid-auto-rows: 180px;
        column-gap: 15px;
        row-gap:15px;
        &__item {
            img {
                width: 100%;
                height: 100%;
            }
            &--ver-1 {
                grid-column-start: 1;
                grid-row-start: span 2;
            }
            &--ver-2 {
                grid-column-start: 3;
                // height: 209%;
                grid-row-start: span 2;
            }
        }

    }


}

@media (max-width:$md ) {
    .destination-mansory-layout {
        
        grid-template-columns: auto auto;
        grid-auto-rows: 180px;
        justify-content: center;
      
        &__item {

            &:nth-child(6) {
                grid-row-start: 3;
            }
            &:nth-child(12) {
                grid-row-start: 7;
            }

            &--ver-2 {
                grid-column-start: 2;
                grid-row-start: span 2;
            }
            &--hor-1 {
                grid-column-start: 2;
            }
            &--nor-2 {
                grid-column-start: 1;  
            }
            
        }
    }
    .destination-mansory-layout__pagination {
        padding: 50px 0 50px;
    }

}
@media(max-width:$sm){
    .destination-mansory-layout
        {
            &__item { 
                width: 80%;
                margin: 15px auto;
            }
        }
}
