@include sectionTittleMiddle (destination-5);

.destination-5 {
    &__item {

        margin-bottom: 50px;
        text-align: center;
        display: block;
        span {
            display: block;
            text-transform: uppercase;
            @include fontface($font-primary-bold,17px,$main-dark,normal);
        }
        &__below-tittle {
            margin-top: 15px;
        }
        &__upper-tittle {
            margin-bottom: 20px;
        }
    }
}