.destination-item-aside {
    padding-top: 60px;
    &__item {
        display: block;
        &:first-child {
            margin: 0;
        }
        margin-top: 40px;
        p {
            margin-top: 10px ;
            color: $dove-gray;
            margin-bottom: 0px;
        }

        &--picture {
            padding: 45px 0 35px;
            img {
                vertical-align: middle;
            }

        }

        &--picture--mansory {

            &__item {
                border-radius: 4px;
                overflow: hidden;
                height: 260px;
                margin-bottom: 30px;
                img {
                    height: 100%;
                    width: 100%;
                }
                &--height2 {
                    height: 270px;
                }
            }
        }

    }

    &__relate-tour {
        @extend .col-4-tour;
        &__item {
            padding: 0 15px;
        }
        .trending-tour-item__group-infor__lasting {
            img {
                display: inline;
            }
        }
        &__tittle {
            margin: -15px 0 40px !important;
        }
        .destination-4-2__arrow {
            &:hover {
                color: $yellow;
            }
        }
    }
    @include sectionTittle(destination-item-aside__relate-tour);
}

