.destination-4__form {
    padding:110px 85px 0 0;
}
.destination-form {
    padding: 75px 70px;
    position: relative;
    .border-img {
        left: 0;
        top: 0;
        height: 100%;
        position:absolute;
    }
    .subsriber-form-tittle {
        h2, span {
           color: $main-dark ;
        }
        &__sub-tittle {
            font-size: 13px;
            color: $dusty-gray;
        } 
        .subsriber-form-tittle__line-under {
            height: 15px;
        }
    }
    &__text {
        border: none !important;
        border-bottom: 1px solid #eeeeee !important;
        line-height: 0px;
        margin-bottom: 20px;
    }
    &__submit {

        height: 45px !important;
        width: 125px !important;
        background-color: $yellow !important;
        text-align: center;
        margin-top: 40px;
    }
}

.destination-4__choices {
    margin-bottom: 25px;
    &__tittle {
        margin-bottom: 0 !important;
    }
    @include sectionTittle(destination-4__choices);

    &__item {
        color: $main-dark !important;
        margin-bottom: 64px;
        display: flex;
        align-items: center;
        span {
            text-transform: uppercase;
            font-weight: bold;
            margin-left: 30px;
        }
        p {
            margin: 0 0 0 30px;
            color: $dove-gray;
            font-size: 14px;
        }

    }
}