
/// -------------------- HEADER-1- RESPONSIVE---------------:


@media(min-width:1024px){
	.header-content1 {
		nav {
    
			li {
				&:hover > ul {
					display: block;
				}
			}
		}
	}
}
@media ( max-width: 1023px ){
	.float-out {
		transform: translateY(0) !important;
		z-index: 2 !important;
	}
	.sub-menu-float-out {
		i {
			transform: rotate(180deg);
		}
		& + ul {
			display: block !important;
		}

	}
    .header-content1 {
	
        position: relative;
        nav {
            padding: 20px;
			transform: translateY(-100%);
            z-index: -2;
            position: absolute;
            width: 100%;
            background-color: rgba($color: #ffffff, $alpha: 0.9);
            left: 0;
            top: 110px;
            // margin-left: -100%;
            box-shadow: 22px 16px 34px -18px rgba(0, 0, 0, 0.41);
        }
        ul {
			padding: 0 15px;
			
        }
        li {
            display: block;
			border-bottom: 1px solid $dusty-gray;
			& > ul {
				padding: 15px;
				position: unset;
				background-color: transparent;
				li {
					width: 100%;
				}
			}
        }      
	
        a:not(.header-logo) {
			i {
				display: block;
			}
            font-size: 15px;
            padding: 10px 0;
            color: $dusty-gray !important;
            display: flex;
            justify-content: space-between;
			align-items: center;
			&:hover {
				background-color: transparent;
				color: $yellow !important;
			}
        }
        &__hamburger {
            display: block;
        }
    }
}
#form-1-area  {
    @media ( max-width: 410px ){
        bottom: 5px;
    }
}
/// -------------------------------- END HEADER-1- RESPONSIVE-------------------------------:




/// -------------------------------- HEADER-2- RESPONSIVE-------------------------------:




@media(min-width:1024px)and(max-width:$xl){
    .header-content2 {
        nav ul li a {
            margin-left: 15px !important;
        }
    }
}
@media (min-width:1024px){
    .header-content2 {
        nav {

            li {
 
                &:hover > ul {
                    top: 110px;
                    height: auto;
                    display: block;
        
                }
            }
        }
    }
}

@media (max-width:1023px){
    .float-out-2 {
        transform: translateY(0) !important;
        visibility: visible !important;
    
    }
    .header-content2 {

        &__call {
       
            padding: 0 15px ;
            width: 100%;
            position: absolute;
            top:  115px;
            visibility: hidden;
            z-index: 6;
            transform: translateY(-500%);
        }
        position: relative;
        nav {
            visibility: hidden;
            transform: translateY(-200%);
            @include boxShadow(4px 26px 50px 11px rgba(0, 0, 0, 0.18));
            padding-top: 60px;
            z-index: 5;
            position: absolute;
            width: 100%;
            left: 0;
            top: 110px;
            background-color: rgba($color: #ffffff, $alpha: 0.9);
            ul {
                padding: 0 15px;
                height: auto !important;
                width: 100%;
            }
            li {
                display: block !important;
                & > ul {
                    padding: 15px;
                    background-color: transparent !important;
                    position: unset !important;
                    border-top: 3px solid transparent !important;
                }

                border-bottom: 1px solid #e6e6e6;
                &:last-child{
                    border-bottom: none;
                }
                width: 100% !important;
                a {
                    padding: 10px 0 !important;
                    display: flex !important;
                    align-items: center;
                    justify-content: space-between;
                    i {
                     
                        display: block !important;
                    }
                    width: 100%;
                    margin: 0 !important;
                }
            }
        }
    }
}




/// -------------------------------- END HEADER-2- RESPONSIVE-------------------------------:


@media (min-width:1024px){
    .header-content3 {
        nav {
            li {
 
                &:hover > ul {
                    top: 110px;
                    height: auto;
                    display: block ;
        
                }
            }
        }
    }
}

@media ( max-width: 1023px ){
    #site-tittle3 {
        display: none !important;
    }
    .header-content3 {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &__mobile-logo {
            display: block;
        }

    }
    .header-3-nav {
        transform: translateY(-150%);
        position: absolute;
        top: 110px;
        z-index: 5;
        width: 100%;
        ul {
            background-color: rgba($color: #ffffff, $alpha: 0.8);
       
            li {
              
                a{
              
                    padding: 10px 0 !important;
                    display: flex !important;
                    align-items: center;
                    justify-content: space-between;
                    border-bottom: 1px solid $dusty-gray !important;
                    .fa-angle-down {
                        display: block !important;
                    }
                }
                & > ul {
                    background-color: transparent;
                    padding: 15px;
                    li {
                        width: 100% !important;
                    }
                }
            }
        }

    }

}


/// -------------------------------- HEADER-3- RESPONSIVE-------------------------------:






/// -------------------------------- HEADER-4- RESPONSIVE-------------------------------:

.header-content2--style2 {
    @media(max-width:$xl) {
        &__account {
            margin-left: 20px !important;
        }
    }
    @media(max-width:1023px){  
        &__account {
            width: 100%;
            margin-left: 0 !important;
            padding: 15px 0 ;
            .header-search {
                width: 100%;
                display: flex;
                align-items: center;
                .header-search__bar {
                    position: unset;
                    top: 0;
                    left: 30px;
                  
                }
            }
            .header-search-show {
                .header-search__bar {
                    width: 100%;
                    padding: 10px 15px;
                }
            }
        }
    }

}
.header-4-upper {

    @media (max-width:$md) {
        &__contact-area {
            line-height: 30px;
            position: absolute;
            top: 165px;
            left: 0;
            z-index: 6;
            background-color: transparent;
            transition: all .5s;
            transform: translateY(-500%);
        }
        &__contact {
            padding-left: 60px;
            display: block;
            margin: 0;
        }
        &__social {
            a:first-child {
                img {
                    margin-left: 0;
                }
            }
            float: left;
        } 
    }
    @media ( max-width: $sm ){
        &__contact {
            padding-left: 15px;
        }
    }

}

.header-content2--style2 {
    @media ( max-width: $xl ){
        .header-2-nav {
            top: 110px ;
        }
        &__account {
            margin: 0;

        }
    }
}