//---------------------------.subsriber-form responsive
@media ( max-width: 768px ){
    .subsriber-form--padding {
        padding: 0;
        .subsriber-form__submit {
            right: 15px;
        }
    }

}
//---------------------------end subsriber-form responsive



//--------------------------- form-1 responsive
.form-area {
	@media (max-width: $lg){
		align-items: unset;
		height: 230px;
		text-align:center;
	}
	@media (max-width: $md){
		height: 300px;
	}
}

.form__item{
	@media (max-width: $xl){
		margin-right: 10px;
		text-align: left;

	}
	@media (max-width: $lg){
		text-align: left;
		margin:30px 15px 0 0 ;
	}
	
}
@media (max-width: 410px){
	.form__item {
		&:first-child {
			margin-top: 20px;
		}
		display: block;
		margin-top: 50px;
	}
	.form-area {
		height:470px ;
	}
}
@media (min-width:411px) and ( max-width: 415px ){

    .form__item {
        margin: 30px 0 0 5px;
    }

}   


//--------------------------- end form-1 responsive