.header-content2 {

    height: 110px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $main-dark !important;
    
    .header-2-nav {

        transition: all 0.5s;
        display: flex;
        align-items: center;
 
        ul {
            height: 110px;
            margin: 0;
            list-style-type: none;
 
        }
        li {
         
            height: 100%;
            position: relative;
            z-index: 5;
            display: inline-flex;
            align-items: center;
            a:not(.account) {
                color: $main-dark;
                font-family: $font-primary-medium;
                display: block;
                padding: 5px 2px;
                margin-left: 33px;
                border-bottom: 2px solid transparent;
                &:hover {
                    font-family: $font-primary-bold; 
                    color: $yellow;
                    border-bottom: 2px solid $yellow;
                }
                i {
                    display: none;
                }
            }
            & > ul {
                background-color: rgba($color: #ffffff, $alpha: 0.8);
                border-top: 3px solid $yellow;
                margin: 0;
                position: absolute;
    
                li{
      
                    width: 180px;
                    border-bottom: 1px solid #e6e6e6;
                    &:last-child {
                        border-bottom: 1px solid transparent;
                    }
                }
                display: none;
            }
        }

    }

    &__call {
  
        transition: all 0.5s;
        img {
            display: inline-block;

        }
        &__phone-number {
            margin-left: 10px;
            p {
                margin-bottom: -5px;
                font-size: 17px;
            }
            span {
                font-family: $font-primary-bold; 
                font-size: 20px;
                color: $yellow;
            }
            display: inline-block;
        }
    }


    &__hamburger {
        
        @extend .header-content1__hamburger;
        span {
            background-color: $main-dark ;
        }
    }
}
.remove-zindex {
    z-index: 0;
}
.faded2 {
	background-color: $dusty-gray !important;
}
