////--------------------- destination-1 reponsive:
@media ( max-width: 1550px ) {
    .top-desti{
        flex-wrap: wrap;
        justify-content: center;
        &__item {
            margin-bottom: 30px;
        }
    }
}



////-----------------------  destination-2 reponsive:

.popular-destination {
    @media (min-width:1652px) {
        height: 600px !important;
        &__item:nth-child(3)  {
            left: 715px !important;
        }  
        &__item:nth-child(5)  {
            left: 936px !important;
        } 
        &__item:nth-child(6)  {
            right:0 ;
            left: unset !important;
            top: 0 !important;
        } 
    }
}


////-----------------------  destination-3 reponsive:

.destination-3 {
    @media (max-width:1024px) {
        &__figure-item {
            padding: 0 0 30px;
        }
        &__figure {
            padding: 30px 0 0;
        }

    }

    @media (max-width:$xl) {
        &__destination {
            flex-flow: wrap;
            justify-content: center;

        }
        &__thumb {
            width: 33.33333%;
            margin-bottom: 60px;
        }
    }
    @media (max-width:767px) {

        &__thumb {
            width: 50%;
            margin-bottom: 60px;
        }
    }
    @media (max-width:474px) {

        &__thumb {
            width: 80%;
    
            margin-bottom: 60px;
            &:hover {
                .destination-3__thumb-info {
                    bottom: -50px !important;
                }
            }
        }

    }

}

////-----------------------  destination-4 choose  reponsive:


@media (max-width:$xl) {
    .destination-4__form {
        padding:110px 0 0 0;
    }
}
@media (max-width:$lg) {
    .border-img {
        width: 100%;
    }

    .destination-4-2__item {
        margin: 0px 0 60px;
    }
}
@media (max-width:$sm) {
    .destination-4__form {
        padding:60px 0 0 0;
    }
    .destination-form {
        padding: 40px 30px;
    }
}
@media (min-width:370px)and(max-width:575px) {
    .destination-4__choices__item {
        width: 60% ;
        margin-left: auto;
        margin-right: auto;
    }
}


@media (max-width:475px) {
    .destination-4-2__content {
        width: 75% ;
        display: inline-block;
    }
}




////-----------------------  .destination-item-aside   reponsive:


.destination-item-aside { 
    @media(max-width:$xl){
        .destination-item-aside__item--picture--mansory__item {
            height: 200px;
        }
    }
    @media(max-width:$md){
        .destination-item-aside__item--picture .row div {
            margin:0 auto 30px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    @media(max-width:$sm){
        .destination-item-aside__item--picture .row div div{
            width: 70%;
            text-align: center;
        }
    }
    
    @media(min-width:$md)and(max-width:$xl) {
        .destination-item-aside__relate-tour__item {
            .trending-tour-item {
                width: 90% !important;
                margin-left: auto;
                margin-right: auto;
            }
        }

    }

}

