.header-content2--style2 {
    @extend .header-content2;

    &__account {
        display: inline-flex;
        margin-left: 80px;
        .account {
            cursor: pointer;
            img {
                vertical-align: middle;
            }
        }
        .header-search {
            position: relative;
            margin-left: 20px;
            &__bar {
                padding: 0;
                width: 0px;
                position: absolute;
                @media(min-width:1024px){
                    right: 0;
                    top: 35px;
                }
                border: none;
                height: 30px;
                border-bottom: 3px solid #e6e6e6;
            }
        }
        .header-search-show {
            .header-search__bar {
                width: 200px;
                padding: 10px 15px;
            }
        }
    }
}

.header-4-upper {
    line-height: 50px;
    height: 50px;
    background-color: $yellow;
    &__contact {
        margin-right: 50px;
    img {
        margin: 0 10px -3px 0;
    }
    }
    &__social {
        img {
            margin-left: 20px;
        }
        float: right;
    }
    &__translater {
        margin-left: 50px;
        float: right;
        select {
            background-color: $yellow;
            border: none;
        }   
        
    }
}

