// ---------------------------------------- video-popup-1 reponsive

@media (max-width:$lg){
    #btn-popup {
        img {
            width: 65px;
            height: 65px;
        }
    }
}
@media (max-width:$md){
    #btn-popup {
        img {
            width: 55px;
            height: 55px;
        }
    }
}
// ---------------------------------------- video-popup-2 reponsive

.video-popup-2 {
    @media (max-width:768px) {
        padding: 0 15px;
    }
}
.video-popup-2__tittle {
    @media (max-width:1024px) {
        #btn-popup2, p {
            margin: 0;
        }
    }
    @media (max-width:768px) {
        #btn-popup2 {
            img {
                width: 44px;
                height: 44px;
            }
            width: 60px;
            height: 60px;
        }
        p {
            font-size: 30px;
        }
        .video-popup-2__tittle__btn-info {
            a {
                height: 30px !important;
                line-height: 30px !important;
            }
        }
    }
    @media (max-width:$sm) {
        p {
            font-size: 20px;
        }
        .video-popup-2__tittle__btn-info {
            a {
                font-size: 10px;
                width: 100px;
            }
        }
    }
}




// ---------------------------------------- preview video-about ius reponsive


@media(max-width:$xl) {
    .preview-video-aboutus__text-behind {
        font-size: 150px ;
        margin-bottom: -60px;
    }
}
@media(max-width:$md) {
    .preview-video-aboutus__text-behind {
        font-size: 100px ;
        margin-bottom: -40px;
    }
}